body{
  background-color: black;
}
.TicTacToeContainer{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 100px;
  color: white;
}
.square{
  border: 1px solid red;
  float:left;
  font-size: 40px;
  height: 100px;
  padding: 0px;
  text-align: center;
  width: 100px;
  margin-right: -1px;
  margin-top: -1px;
  cursor: pointer;
}
